#reserve-box-Content {
  input.error {
    border: 3px solid red;
  }
}

.lightbox-choose-0 {
  transform: translateX(0);
}

.lightbox-choose-1 {
  transform: translateX((-100%/3));
}

.lightbox-choose-2 {
  transform: translateX((-100%/3) * 2);
}

#lightbox,
#lightboxCK {
  display: none;
}

#lightboxCK:checked ~ #lightbox {
  display: flex;
}
