@mixin Carouselimg() {
}

.Carousel {
  width: 100%;
  position: relative;
  margin: auto;
  height: 800px;
  overflow: hidden;
  background-color: gray;

  @media screen and(max-width:900px) {
    height: 600px;
  }

  @for $i from 0 through 5 {
    &.choose-#{$i} ul {
      transform: translateX((-100% / 6) * $i);
    }
  }

  ul {
    padding: 0;
    margin: 0%;
    width: 600%;
    height: 100%;
    list-style-type: none;
    transition: transform 2s ease-in-out;
  }

  li {
    width: (100%/6);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    float: left;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .HomeContent {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0%;
    display: flex;
    justify-content: center;
    align-items: center;

    .Homeflex {
      .HomeTop {
        text-align: center;
      }
      .HomeBottom {
        margin-top: 20px;
        display: flex;

        @media screen and(max-width:900px) {
          display: block;
        }

        .left {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 30%;
          padding-right: 10%;
          border-right: 1px white solid;

          @media screen and(max-width:900px) {
            border-right: none;
            border-bottom: 1px white solid;
            width: 90%;
            padding: 0 0 1rem 0;
            margin: auto;
            margin-bottom: 1rem;
          }

          img {
            width: 1.8rem;
            margin-right: 0.5rem;
          }
        }

        .right {
          width: auto;
          padding-left: 1.5rem;

          @media screen and(max-width:900px) {
            padding: 0%;
            margin: auto;
          }

          p {
            color: white;
            white-space: nowrap;
            display: flex;
            margin: 0%;

            &:not(:last-child) {
              margin-bottom: 1rem;
            }

            &::before {
              margin-right: 1rem;
              content: "";
              display: inline-block;
              height: 1.5rem;
              background-repeat: no-repeat;
              background-size: 100%;
              width: 1.5rem;
            }

            &:nth-child(1)::before {
              background-image: url(./img/phone-alt-solid.svg);
            }
            &:nth-child(2)::before {
              background-image: url(./img/envelope-solid.svg);
            }
            &:nth-child(3)::before {
              background-image: url(./img/home-solid.svg);
            }
          }
        }
      }
    }
  }
}
