@mixin loading($choose: 1) {
  .color:nth-child(#{$choose}) {
    background-color: gray;
    animation: top 1.6s (0.2s * $choose) infinite;
  }
}

.loading {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;

  div {
    width: 10px;
    height: 60px;
    background-color: black;
    margin: 0 5px;
    transform: scaleY(0.5);
    opacity: 0.3;
  }

  @for $i from 1 through 4 {
    @include loading($i);
  }
}

@keyframes top {
  0% {
    transform: scaleY(0.5);
  }
  25% {
    transform: scaleY(1.3);
    opacity: 1;
  }
  60% {
    transform: scaleY(0.5);
    opacity: 0.3;
  }
  70% {
    transform: scaleY(0.5);
  }
}
