.room {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 500px;
  grid-column-gap: 2vw;
  grid-row-gap: 2rem;
  margin-top: -100px;
  margin-bottom: 100px;

  @media screen and (max-width: 1200px) {
    padding: 0% 10%;
    grid-template-columns: 1fr 1fr;
  }

  @media screen and (max-width: 900px) {
    grid-auto-rows: 400px;
    margin-top: 50px;
    grid-template-columns: 1fr;
  }

  .roomlist {
    width: 100%;
    box-shadow: 1px 1px 6px 0px #aaa;
    -ms-box-shadow: 1px 1px 6px 0px #aaa;
    -moz-box-shadow: 1px 1px 6px 0px #aaa;
    -webkit-box-shadow: 1px 1px 6px 0px #aaa;
    filter: progid:DXImageTransform.Microsoft.Shadow(Strength=0, Direction=0, Color='#aaa'),
        progid:DXImageTransform.Microsoft.Shadow(Strength=0, Direction=90, Color='#aaa'),
        progid:DXImageTransform.Microsoft.Shadow(Strength=0, Direction=180, Color='#aaa'),
        progid:DXImageTransform.Microsoft.Shadow(Strength=0, Direction=270, Color='#aaa');

    // height: 500px;
    overflow: hidden;
    color: black;
    text-decoration: none;

    &:hover {
      .top {
        height: 70%;
      }

      .bottom p {
        height: 50%;
        font-size: 1rem;
      }
    }

    .top {
      width: 100%;
      height: 80%;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      background-color: lightgray;
      transition: height 0.3s ease-in-out;

      @media screen and (max-width: 900px) {
        height: 70%;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .bottom {
      height: 30%;
      p {
        font-size: 1.2rem;
        transition: all 0.3s ease-in-out;
        margin: 0%;
        margin-left: 2rem;
        height: 50%;
        display: flex;
        align-items: center;
      }
      .normalDayPrice,
      .holidayPrice {
        height: 40%;
        display: inline-flex;
        justify-content: center;
        align-items: flex-end;
        width: 50%;
        font-size: 1rem;

        span {
          font-size: 0.8rem;
        }
      }

      .normalDayPrice.focus,
      .holidayPrice.focus {
        font-size: 2rem;
      }
    }
  }
}
