@charset "UTF-8";
.calendar {
  margin-top: 200px;
  width: 100%;
  height: 100%;
  background: #f7f7f7;
  box-shadow: 0 0 12px 0px lightgray;
  text-align: center;
  margin: auto;
  padding-bottom: 1rem; }
  .calendar .title {
    position: relative; }
    .calendar .title .titleflex {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 1.8rem;
      padding: 1rem 0; }
    .calendar .title h1,
    .calendar .title h2 {
      margin: 0; }
    .calendar .title .prev,
    .calendar .title .next {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 50px;
      top: 0;
      text-decoration: none;
      font-size: 1.2rem;
      color: #999;
      cursor: pointer;
      user-select: none;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none; }
    .calendar .title .prev {
      left: 0; }
    .calendar .title .next {
      right: 0; }
  .calendar .body-list ul {
    width: 100%;
    font-family: arial;
    font-weight: bold;
    font-size: 14px;
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(7, 14.28571%); }
  .calendar .body-list ul li {
    width: 100%;
    padding: 1rem;
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    text-align: center; }
  .calendar .lightgrey {
    color: #a8a8a8;
    /*浅灰色*/ }
  .calendar .darkgrey {
    color: #72777d;
    /*深灰色*/ }
  .calendar .green {
    color: #72777d;
    /*绿色*/ }
  .calendar .greenbox {
    background-size: 6px 6px;
    background-color: white;
    background-image: linear-gradient(45deg, rgba(89, 89, 89, 0.7) 25%, transparent 25%, transparent 50%, rgba(89, 89, 89, 0.7) 50%, rgba(89, 89, 89, 0.7) 75%, transparent 75%, transparent); }
